th {
  text-transform: uppercase;
}
.table-heading {
  text-align: left;
  font-size:x-large;
  font-weight: bold;
}


h3,
h4 {
  font-weight: normal;
  color: black; 
}
h2 {
  color: #000;
}

.regNotes {
  color: #c01921;
}
.regNotes p {
  margin: 0;
  font-size: 20px;
}
.button-container {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
}
.payment-button a {
  text-decoration: none;
  color: white;
  font-size: 20px;
  font-weight: 600;
}
.payment-button {
  padding: 10px 20px;
  border-radius: 25px;
  border: none;
  background-color: green;
}
.payment-button:hover {
  background-color: #4caf50;
}
.register-button a {
  text-decoration: none;
  color: Black;
  font-size: 20px;
  font-weight: 600;
}
.register-button {
  padding: 10px 20px;
  border-radius: 25px;
  border: none;
  background-color: gold;
}
.register-button:hover {
  background-color: goldenrod;
}
.url{
  color: blue;
}

@media only screen and (max-width: 768px) {
  .regFee {
    width: 90%; /* Adjust as needed for smaller screens */
  }

  .regHead h2 {
    font-size: 28px;
  }

  .regDataField {
    margin: 2rem 0;
  }

  table {
    width: 100%;
  }

  th, td {
    flex: 1 1 50%;
    text-align: center;
  }

  .table-heading {
    font-size: large;
  }
}